<template>
  <a-form :form="form" @submit="handleSubmit">
    <a-form-item :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }" label="客户名称">
      <a-input
        v-decorator="[
          'userName',
          {rules: [{ required: true, message: '请输入客户名' }]}
        ]"
      />
    </a-form-item>
    <a-form-item :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }" label="稿件名称">
      <a-input
        v-decorator="[
          'pieceName',
          {rules: [{ required: true, message: '请输入稿件名' }]}
        ]"
      />
    </a-form-item>
    <a-form-item :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }" label="规格">
      <a-select
        @change="handleSelectChange"
        placeholder="选择稿件规格"
        v-decorator="[
          'size',
          {rules: [{ required: true, message: '请选择规格' }]}
        ]"
      >
        <a-select-option value="6">6开</a-select-option>
        <a-select-option value="8">8开</a-select-option>
        <a-select-option value="16">16开</a-select-option>
        <a-select-option value="32">32开</a-select-option>
      </a-select>
    </a-form-item>
    <a-form-item :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }" label="状态">
      <a-select
        @change="handleSelectChange"
        placeholder="选择稿件状态"
        v-decorator="[
          'status',
          {rules: [{ required: true, message: '请选择状态' }]}
        ]"
      >
        <a-select-option value="0">等待</a-select-option>
        <a-select-option value="1">设计</a-select-option>
        <a-select-option value="2">完成</a-select-option>
        <a-select-option value="3">交付</a-select-option>
      </a-select>
    </a-form-item>
    <a-form-item :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }" label="色数">
      <a-select
        @change="handleSelectChange"
        placeholder="选择色数"
        v-decorator="[
          'colors',
          {rules: [{ required: true, message: '请选择色数' }]}
        ]"
      >
        <a-select-option value="1">单色</a-select-option>
        <a-select-option value="2">双色</a-select-option>
        <a-select-option value="3">三色</a-select-option>
        <a-select-option value="4">四色</a-select-option>
      </a-select>
    </a-form-item>
    <a-form-item
      :label-col="{ span: 5 }"
      :wrapper-col="{ span: 12 }"
      label="选择签单日期"
      v-bind="formItemLayout"
    >
      <a-date-picker v-decorator="['signDate', config]"/>
    </a-form-item>
    <a-form-item
      :label-col="{ span: 5 }"
      :wrapper-col="{ span: 12 }"
      label="页数"
      v-bind="formItemLayout"
    >
      <a-input-number
        :max="10000"
        :min="1"
        v-decorator="['pages', {rules: [{ required: true, message: '请选择页数' }]}]"
      />
    </a-form-item>
    <a-form-item
      :label-col="{ span: 5 }"
      :wrapper-col="{ span: 12 }"
      label="单价"
      v-bind="formItemLayout"
    >
      <a-input-number
        :max="10000"
        :min="1"
        v-decorator="['price', {rules: [{ required: true, message: '请选择单价' }]}]"
      />
    </a-form-item>
    <a-form-item :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }" label="公司地址">
      <a-input
        v-decorator="[
          'address',
          {rules: [{ required: true, message: '请输入公司地址' }]}
        ]"
      />
    </a-form-item>
    <a-form-item :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }" label="联系人">
      <a-input
        v-decorator="[
          'contacts',
          {rules: [{ required: true, message: '请输入联系人名字' }]}
        ]"
      />
    </a-form-item>
    <a-form-item
      v-bind="formItemLayout"
      label="联系方式"
      :label-col="{ span: 5 }"
      :wrapper-col="{ span: 12 }"
    >
      <a-input
        v-decorator="[
          'phone',
          {
            rules: [{ required: true, message: '请输入电话号码' }],
          }
        ]"
      >
        <a-select
          slot="addonBefore"
          v-decorator="[
            'phone',
            { initialValue: '86' }
          ]"
        >
          <a-select-option value="86">+86</a-select-option>
          <a-select-option value="87">+87</a-select-option>
        </a-select>
      </a-input>
    </a-form-item>
     <a-form-item
      v-bind="formItemLayout"
      label="E-mail"
      :label-col="{ span: 5 }"
      :wrapper-col="{ span: 12 }"
    >
      <a-input
        v-decorator="[
          'email',
          {
            rules: [{
              type: 'email', message: '你输入的不是email',
            }, {
              required: true, message: '请输入email',
            }]
          }
        ]"
      />
    </a-form-item>
    <a-form-item :wrapper-col="{ span: 12, offset: 5 }">
      <a-button html-type="submit" type="primary">提交</a-button>
    </a-form-item>
  </a-form>
</template>

<script>
export default {
  data() {
    return {
      formItemLayout: {
        labelCol: {
          xs: { span: 24 },
          sm: { span: 8 }
        }
      },
      form: this.$form.createForm(this),
      config: {
        rules: [{ type: "object", required: true, message: "请选择日期" }]
      }
    };
  },
  methods: {
    handleSubmit(e) {
      e.preventDefault();
      this.form.validateFields((err) => {
        if (err) {
          return;
        }
      });
    }
  }
};
</script>